@import "../utils/utils";

a {
    color: inherit;
    text-decoration: none;
}
  
h1,
h2,
h3,
h4,
p {
    margin: 0;
}
  
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
  
img {
    display: block;
    max-width: 100%;
    height: auto;
}
  
button {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
    background-color: $basic-white;
}

.container {
    margin: 0 auto;
	padding: 0 16px;

    @media screen and (min-width: $mobile) {
        width: $mobile;
    }

    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding: 0 24px;
    }

    @media screen and (min-width: $desktop) {
        width: $desktop;
    }
}

section {
    padding-top: 60px;

    @media screen and (min-width: $tablet) {
        padding-top: 100px;
    }
}

.title-wrapper {
    display: flex;
    align-items: center;
    gap: 14px;
    margin-bottom: 32px;

    @media screen and (min-width: $tablet) {
        gap: 16px;
        margin-bottom: 30px;
    }

    @media screen and (min-width: $desktop) {
        margin-bottom: 26px;
    }
}

.title {
    @include font (700, 24px, calc(29/24));
    color: $primary-color;
    text-transform: uppercase;


    @media screen and (min-width: $tablet) {
        font-size: 44px;
        line-height: calc(54/44);
    }
}

.line {
    @extend %line;
}